.filters {
    position: absolute;
    background-color: #25273c;
    width: 150px;
    height: 150px;
    left: 14%;
    top: 33%;
    border-radius: 10px;
    box-sizing: border-box;
    padding: 15px 15px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    cursor: pointer;
}

.filter {
    color: #fff;
    font-size: 18px;
    line-height: 20px;
}

.filter:hover {
    opacity: 0.6;
    transition: opacity 0.3s ease-out;
}

.active {
    color: rgba(15, 144, 162, 0.57);
}